import { Component, Renderer2, Injectable } from '@angular/core';
import { ScrollSpyModule, ScrollSpyService } from 'ngx-scrollspy';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'app';
  constructor(private render: Renderer2, private router: Router) {
    // this.scroller();
  }

  // scroller() {
  //   this.render.listen('window', 'scroll', event => {
  //     //  console.log( window.pageYOffset);
  //     // console.log(event.target.window.pageYoffset);
  //   });
  // }
  ngOnInit() {
    this.router.events.subscribe(evt => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });
  }
}
