import { Component, OnInit } from '@angular/core';
import { CourseDescComponent } from '../course-desc/course-desc.component';

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html',
  styleUrls: ['./courses.component.scss']
})
export class CoursesComponent implements OnInit {
public  content=['Receive information about new courses, workshops & community events in your city' , 'Learn for less with exclusive offers on upcoming workshops & courses','Gain access to networking and hiring opportunities in business, design & tech'];
public now:any;
public eventDate:any;
public currentTime:any;
public eventTime:any;
public remTime:any;
public second:any;
public minute:any;
public hour:any;
public days:any;
  constructor() { }

  ngOnInit() {
    this.Timer();

    this.data();
  }

  data(){

  
 
}
  Timer()
  {
this.now=new Date();
this.eventDate=new Date(2018,9,1);
this.currentTime=this.now.getTime();
this.eventTime=this.eventDate.getTime();
this.remTime=this.eventTime-this.currentTime;
this.second=Math.floor(this.remTime/1000);
this.minute=Math.floor(this.second/60);
this.hour=Math.floor(this.minute/60);
this.days=Math.floor(this.hour/24);
this.hour%=24;
this.minute%=60;
this.second%=60;
setTimeout(()=>{  
  this.Timer();
},1000);
  }
}
