import { Component, OnInit, ViewChild } from '@angular/core';

declare var $: any;

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  @ViewChild('enrollForm') enrollform;

  public register = [];
  constructor() {}

  ngOnInit() {
    this.register = [
      'Receive information about new courses, workshops & community events in your city',
      'Learn for less with exclusive offers on upcoming workshops & courses',
      'Gain access to networking and hiring opportunities in business, design & tech'
    ];
  }

  isNumber(evt) {
    evt = evt ? evt : window.event;
    const charCode = evt.which ? evt.which : evt.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  ResetForm() {
    this.enrollform.reset();
    $('#myModal').modal('hide');
    this.enrollform.form.markAsPristine();
  }

  SubmitForm() {
    debugger;
    if (this.enrollform.valid) {
      const $form = $('form.lets-talk'),
        url =
          // 'https://script.google.com/macros/s/AKfycbzVgL431QQuuNNkLaEsLQmZbl6vQtuej-LEGc43QPwceLsDffc/exec';
          // 'https://script.google.com/macros/s/AKfycbx0u3wSVQg1gg6atR54VFIdcudFghAm_93A6ou1KsD4MVvSGZY/exec';
          // new
          'https://script.google.com/macros/s/AKfycbyqBFDf94xy97-pMjxMuLjhD1TEcpXWierIb1sX-yrOQ8VJylI/exec';

      const finaldata = $form.serialize();

      const jqxhr = $
        .ajax({
          url: url,
          method: 'GET',
          dataType: 'json',
          data: finaldata
        })
        .success(
          $('#myModal').modal('hide'),
          $('#thankYou').modal('show'),
          $('#contact').modal('hide'),
          $('form')[0].reset()
        );
    }
  }
}
