import { Component, OnInit, ViewChild } from '@angular/core';
declare var $: any;

@Component({
  selector: 'app-landing-cards',
  templateUrl: './landing-cards.component.html',
  styleUrls: ['./landing-cards.component.scss']
})
export class LandingCardsComponent implements OnInit {
  public card = [{}];
  public leveltext: any;
  @ViewChild('enrollForm') enrollform;

  public modaltitle: any;
  public range: any;
  public register: any;
  constructor() {}

  ngOnInit() {
    this.register = [
      'Receive information about new courses, workshops & community events in your city',
      'Learn for less with exclusive offers on upcoming workshops & courses',
      'Gain access to networking and hiring opportunities in business, design & tech'
    ];
    this.card = [
      {
        title: 'Become a UI/UX designer from scratch',
        level: 'Beginner',
        value: 30,
        jobtype: 'Full-Time',
        location: 'Bangalore',
        date: '2-July-2018',
        duration: '17 Days',
        seat: '20',
        close: '27 June',
        link: '/beginner'
      },
      {
        title: 'UI/UX: Stratergy and tactics',
        level: 'Intermediate',
        value: 60,
        jobtype: 'Full-Time',
        location: 'Bangalore',
        date: '2-July-2018',
        duration: '28 Days',
        seat: '20',
        close: '27 June',
        link: '/intermediate'
      },
      {
        title: 'UI/UX: Industry immersive course',
        level: 'Advance',
        value: 100,
        jobtype: 'Full-Time',
        location: 'Bangalore',
        date: '2-Jul-2018',
        duration: '17 Days',
        seat: '20',
        close: '27 June',
        link: '/expert'
      }
    ];
  }
  ScrollUp() {
    window.scroll(0, 0);
  }
  Modalpage(z, x, y) {
    this.modaltitle = z;
    this.leveltext = x;
    this.range = y;
  }

  SubmitForm() {
    if (this.enrollform.valid) {
      const $form = $('form.enroll-me-form'),
        url =
          'https://script.google.com/macros/s/AKfycbzVgL431QQuuNNkLaEsLQmZbl6vQtuej-LEGc43QPwceLsDffc/exec';

      const finaldata = $form.serialize();

      const jqxhr = $
        .ajax({
          url: url,
          method: 'GET',
          dataType: 'json',
          data: finaldata
        })
        .success(
          $('#myModal').modal('hide'),
          $('#thankYou').modal('show'),
          $('#contact').modal('hide'),
          this.enrollform.reset()
        );
    }
  }

  ResetForm() {
    this.enrollform.reset();
    $('#myModal').modal('hide');
    this.enrollform.form.markAsPristine();
  }
  isNumber(evt) {
    evt = evt ? evt : window.event;
    const charCode = evt.which ? evt.which : evt.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
}
