import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';

declare var $: any;

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  @ViewChild('subscribeForm') subscribeForm;

  constructor() {}

  ngOnInit() {}

  SubmitForm() {
    if (this.subscribeForm.valid) {
      const $form = $('form#subscriber'),
        url =
          'https://script.google.com/macros/s/AKfycbzIOPtsu2OltB8O76HYworB-PHEz5VvJc6pX7JC1N2ASFCpMCU/exec';

      const finaldata = $form.serialize();

      const jqxhr = $
        .ajax({
          url: url,
          method: 'GET',
          dataType: 'json',
          data: finaldata
        })
        .success(
          $('#myModal').modal('hide'),
          $('#thankYou').modal('show'),
          $('#contact').modal('hide'),
          this.subscribeForm.reset()
        );
    } else {
      alert('form is not valid');
    }
  }
}
